	module.exports = {"data":{"post":{"html":"<p><b>Update:</b> Christopher's fever is down to 38&deg;C (100.4&deg;F), he's stable, and already flirting with the nurses. :-)</p>\n<p>Things are only getting worse here. We took Christopher to the pediatrician on Thursday morning, and were told to keep giving him aspirin and to come back Monday if he didn't get any better. He didn't get better, and the aspirin didn't keep the fever down. We called the pediatrician today, who said not to panic and to wait another day. </p>\n<p>We didn't follow her advice. Mama took Christopher to the nearest children's clinic and they diagnosed pneumonia. He will be in the hospital for the next 10 days. </p>\n<p>As for me, I'm on antibiotics and bed rest for the next few days, so I can't even go visit him.</p>\n<p>Thanks, <a href=\"http://andrea.editthispage.com/\">Andrea</a>, for the get-well wishes. And yes, we did notice the <a href=\"http://andrea.editthispage.com/pictures/viewer$1334\">hippos</a> from the Expo :-)</p>","fields":{"title":"Not any better","date":"21 Oct 2000","url":"https://papascott.de/2000-10-21-not-any-better/"}}},"pathContext":{"slug":"/2000-10-21-not-any-better/","prev":{"fields":{"title":"Return","date":"20 Oct 2000","slug":"/2000-10-20-return/"}},"next":{"fields":{"title":"Other news","date":"22 Oct 2000","slug":"/2000-10-22-other-news/"}}}}

